class Detail extends React.Component {
    render() {
        return (
            <div>
                 <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.push('/')}
            >商品详情</NavBar>
            <img src={require('./images/wan.png')} style={{width:"100%"}}/>
            <div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
            <TabBar 
            unselectedTintColor="#949494"
			tintColor="#33A3F4"  //选中字体颜色
            barTintColor="white"  //背景颜色
            >
                <TabBar.Item
                title="店铺"
                key="shop"
                icon={<i className='iconfont icon-dianpu'></i>}
                // selectedIcon={<i className='iconfont icon-Homehomepagemenu1'></i>}
                // selected={pathname === '/home'}
                // onPress={() => {
                //     this.props.history.push('/home')
                // }
                // }
                data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.Item
                title="客服"
                key="clerk"
                icon={<i className='iconfont icon-kefu'></i>}
                // selectedIcon={<i className='iconfont icon-Homehomepagemenu1'></i>}
                // selected={pathname === '/home'}
                // onPress={() => {
                //     this.props.history.push('/home')
                // }
                // }
                data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.item
                title="收藏"
                key="collection"
                icon={<i className='iconfont icon-shoucang'></i>}
                data-seed="logId"
                >
                </TabBar.item>
                <TabBar.item
                    icon={<button style={{color:'white',width:'100px',height:'35px',background:'#ff6500',border:0,borderRadius:'50px 0 0 50px'}}>加入购物车</button>}
                >
                </TabBar.item>
                <TabBar.item
                    icon={<button style={{color:'white',width:'100px',height:'35px',background:'rgb(255,113,2)',border:0,borderRadius:' 0 50px 50px 0'}}>立即购买</button>}
                ></TabBar.item>
            </TabBar>
            </div>
            </div>
        )
    }
}

export default Detail;
